<template>
    <a-col class="badgecard" :xs="10" :sm="8" :md="4" :lg="4" :xl="4">
        <a-row class="icon">
            <img :src="iconPath" alt="icon">
        </a-row>
        <a-row class="text">
            <h3 id="title"> {{ title }} </h3>
            <p id="body"> {{ text }} </p>
        </a-row>
    </a-col>
</template>

<script>
    export default{
        props: {
            icon: String,
            text: String,
            title: String,
        },
        computed: {
            iconPath() {
            return require(`@/assets/icons/${this.icon}`);
            },
        }
    }
</script>

