    @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;600;800&display=swap');
    .badgecard {
        padding: 1vh 1vw;
        align-items: center;
        justify-content: center;
        color: #f0f0f0;
        height: fit-content;
        width: fit-content;
    }
    .icon {
        background-color: #394435;
        border-radius: 100%;
        justify-content: center;
        height: 100px;
        width: 100px;
        margin: auto;
    }
    #title {
        font-weight: 800;
        color: #394435;
        font-family: 'Montserrat', 'Courier New', Courier, monospace;
    }
    .text {
        padding: 1vh 1vw;
        margin-top: 1.5vh;
        height: fit-content;
        width: fit-content;
        min-width: 150px;
        justify-content: center;
        text-align: center;
