<template>
    <a-col class="content" :xs="16" :sm="12" :md="12" :lg="8" :xl="6">
        <!-- <img src="../" alt=""> -->
        <h1 class="Valor">12x de R$9,70</h1>
        <h2 class="Valor">ou R$ 97,00 a vista</h2>

        <a-form
            layout='vertical'
            :model="formState"
            name="basic"
            autocomplete="off"
            @finish="enviarDados"
        >
            <a-form-item
                name="nome"
                :validateStatus="errors.nome ? 'error' : ''"
                :help="errors.nome"
            >
                <a-input v-model:value="formState.nome"  placeholder="Informe seu nome e sobrenome:"/>
            </a-form-item>

            <a-form-item
                name="contato"
                :validateStatus="errors.contato ? 'error' : ''"
                :help="errors.contato"
                
            >
            <a-input v-model:value="formState.contato" placeholder="Informe seu contato:"/>
            </a-form-item>

            <a-form-item name="check">
                <a-checkbox class="check" v-model:checked="formState.check" >Li e concordo com os Termos</a-checkbox>
            </a-form-item>

            <a-form-item >
                <a-button :class="{ 'enabled-button': formState.check }" class="bt" type="primary" html-type="submit" :disabled="!formState.check">GARANTIR VAGA</a-button>
            </a-form-item>
        </a-form>
    </a-col>
</template>

<script>

import {db} from '../../firebase/index'
import { addDoc, collection } from 'firebase/firestore/lite';
import { reactive } from 'vue';

    
    export default {
    setup() {
        const formState = reactive({
            nome: '',
            contato: '',
            check: false,
        });

        const errors = reactive({
            nome: '',
            contato: '',
        });

        const enviarDados = async () => {

            if (formState.nome == '' && formState.contato == '') {
                errors.nome  = 'Campo obrigatório.'
                errors.contato = 'Campo obrigatório.'
                return;
            }

            if (!validarNome(formState.nome)) {
                return;
            } 
            if (!validarContato(formState.contato)) {
                return;
            }

            try {
                await addDoc(collection(db, "data_users"), {
                    nome: formState.nome,
                    contato: formState.contato
                });

                formState.nome = "";
                formState.contato = "";
                formState.check = false;
                window.open("https://chk.eduzz.com/2005020", "_blank");
            } catch (error) {
                console.error("Erro ao adicionar documento: ", error);
            }
        };

        const validarNome = nome => {
            const regex = /^[a-zA-Z\u00C0-\u017FçÇ\s'-]+$/;
            if (nome == "") {
                errors.nome = 'Campo obrigatório.';
                return false;
            } else if (!regex.test(nome) || nome.length <= 5) {
                errors.nome = 'O nome deve ter pelo menos 6 caracteres e conter apenas letras.';
                return false;
            } else {
                errors.nome = '';
                return true;
    }
        };

        const validarContato = contato => {
            const regex = /^(?:(?:\+|00)?(55)\s?)?(?:\(?([1-9][0-9])\)?\s?)?(?:(9\d|[2-9])\d{3})-(\d{4})$/;
            
            if (contato == '') {
                errors.contato = 'Campo obrigatório.'
            }
            else if (!regex.test(contato)) {
                errors.contato = 'O contato deve seguir este padrão: 99 99999-9999.';
                return false;
            } else {
                errors.contato = '';
                return true;
            }
        };

            return {
                formState,
                errors,
                enviarDados,
            };
        },
    };

</script>

<style scoped>
    .content {
        height: max-content;
        padding: 1rem;
        background: rgba( 255, 255, 255, 0.1 );
        box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.2 );
        backdrop-filter: blur( 4px );
        -webkit-backdrop-filter: blur( 8px );
        border-radius: 10px;
        border: 1px solid rgba(95, 95, 95, 0.18);
    }

    .bt {
        width: 100%;
    }
    
    .enabled-button {
        background-color: #66B649;
        border-radius: 8px;
        border: none;
        padding: 0rem 2rem;
        color: aliceblue;
        font-weight: bolder;
        font-size: 1.1rem;
        cursor: pointer;
        width: 100%;
    }
    .enabled-button:hover {
        background-color: #57a03c;
        box-shadow: 1px 1px 8px #e3ebe06f;
    }

    #input {
        margin: 8px 0;
        height: 6vh;
    }
    .Valor{
        color: aliceblue;
        font-weight: bold;
    }
    .check {
        color: white;
        padding: 1vh 0;
    }
</style>
